import React, { useEffect, useState } from "react";

import { fetchWeatherGeo } from "./api/fetchWeatherUnsplash";
import WeatherCardGeo from "./components/WeatherCardGeo";

function App() {
  
  const [weather, setWeather] = useState({});

  useEffect(() => {
    if (window.matchMedia("(prefers-color-scheme: dark)").matches) {
      document.documentElement.classList.add("dark");
    } else {
      document.documentElement.classList.remove("dark");
    }

    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(async function (position) {
        console.log(position);
        const { latitude, longitude } = position.coords;
        
        const data = await fetchWeatherGeo(latitude, longitude);
        setWeather(data);
      });
    }
  }, []);


  return (
    <div className="container grid lg:grid-cols-3 lg:p-2 lg:mx-auto lg:my-2 sm:grids-cols-1 p-12 ">
  {/*className="container grid sm:grid-cols-1 sm:grid-flow-row-dense sm:gap-2 lg:grid-cols-6 lg:gap-5 lg:grid-rows-3">*/}
      <div className="lg:col-start-2 lg:col-span-1 sm:col-span-1 sm:p-4 sm:mx-4">
      {
        (weather && weather.main && weather.sys) && (<WeatherCardGeo bgImage={""} data={weather}/>)
      }      
      </div>
    </div>
  );
}

export default App;
